import Header from "./component/Header";
import Bg from "./component/Bg";
import Pic from './component/Pic';
import Go from './component/Go';
import Card from './component/Card';
import Detail from "./component/Detail"
import data from "./component/data";
import "./App.css";
import axios from 'axios';
import { useState, useEffect } from "react";
import { Routes, Route, useNavigate, Outlet } from 'react-router-dom'




function App() {
  let [items, itemss] = useState(data);
  let [button, setButton] = useState(1);
  let [fade, setFade] = useState("");

  useEffect(() => {
    setTimeout(() => {
      setFade("end");
    }, 100);
    return () => {
      setFade("");
    };
  }, [App]);

  let buttonClick = (() => {
    if (button === 1) {
      axios.get('https://ttochino.github.io/data1.json')
        .then((data) => {
          let copy = [...items, ...data.data];
          itemss(copy);
        })
        .catch(() => {
          console.log("데이터가 로드되지 않습니다.")
        })
    } else if (button === 2) {
      axios.get('https://ttochino.github.io/data2.json')
        .then((data) => {
          let copy = [...items, ...data.data];
          itemss(copy);
        })
        .catch(() => {
          console.log("데이터가 로드되지 않습니다.")
        })
    } else {
      alert("더이상 새로운 위시가 없습니다.")
      { <div> appp.style.visibility = 'hidden' </div> };
    }
  })

  return (
    <div className={"start " + fade}>
      <Header />
      <Routes>
        <Route path="/" element={
          <>
            <Bg />
            <div className='App'>
              <button className='bt bt1'
                onClick={() => {
                  let copy = [...items];
                  itemss(copy.sort((a, b) => a.id - b.id))
                }}>
                기본 정렬
              </button>
              <button className='bt bt1'
                onClick={() => {
                  let copy = [...items];
                  itemss(copy.sort((a, b) => a.title.localeCompare(b.title)))
                }}>
                오름차순 정렬
              </button>
              <button className='bt bt1'
                onClick={() => {
                  let copy = [...items];
                  itemss(copy.sort((a, b) => a.price - b.price))
                }}>
                최저가 정렬
              </button>
            </div>
            <div className="container">
              {items.map(function (a, i) {
                return (
                  <Card items={items[i]} i={i + 1} />
                );
              })}
            </div>
            <div className='App'
              onClick={buttonClick}>
              <button className="bt bt1"
                onClick={() => {
                  setButton(button + 1)
                  console.log(button)
                }}
              >상품 더보기
              </button>
            </div>
          </>} />
        <Route path="/detail/:id" element={<Detail items={items} />} />
        <Route path="/about" element={<About />} >
          <Route path="pic" element={<Pic />} />
          <Route path="go" element={<Go />} />
        </Route>
        <Route path="*" element={<div>잘못된 경로로 접속하였습니다.</div>} />
      </Routes >
    </div>
  );
}

function About() {
  let navigate = useNavigate();

  return (
    <>
      <div className='About'>
        <h4> 새로운 취미 생활은 돈이 많이 든다.</h4>
        <p>하지만 ...</p>
        <div className='bbt'>
          <button className="bt bt1 bt2"
            onClick={() => {
              navigate("/about/pic");
            }}>picture</button>
          <button className="bt bt1 bt2" onClick={() => {
            navigate("/about/go");
          }}>Go Out</button>
        </div>
        <Outlet></Outlet>
      </div >
    </>
  )
}

export default App;
