import "../App.css";
import "bootstrap/dist/css/bootstrap.min.css";

import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";

import Nav from "react-bootstrap/Nav";
import styled from "styled-components";

let Bkbt = styled.button`
  background: firebrick;
  color: white;
  padding: 3px;
  margin: 7px;
  border: none;
  border-radius: 7px;
  font-size: 1.1rem;
`;

let Mmodal = styled.div`
  background: yellow;
  color: red;
  padding: 20px;
  text-align: center;
  font: normal bold 1.6rem / 1.5 "Noto Serif KR", serif;
  font-weight: 700;
`;

function Detail(props) {
  let [modal, setModal] = useState(true);
  let [num, setNum] = useState("");
  let [page, setPage] = useState(0);
  let [fade, setFade] = useState("");

  useEffect(() => {
    setTimeout(() => {
      setFade("end");
    }, 100);
    return () => {
      setFade("");
    };
  }, [Detail]);

  useEffect(() => {
    let timer = setTimeout(() => {
      setModal(false);
    }, 3000);
    return () => {
      clearTimeout(timer);
    };
  }, []);

  useEffect(() => {
    if (isNaN(num) == true) {
      alert("숫자를 입력해주세요");
    }
  }, [num]);

  let { id } = useParams();
  let finder = props.items.find(function (x) {
    return x.id == id;
  });

  return (
    <div className={"start " + fade}>
      {modal === true ? <Mmodal>지갑이 안전할 때 사자...</Mmodal> : null}
      <div className="container">
        <div className="details">
          <img
            className="img-detail"
            src={process.env.PUBLIC_URL + "/img" + finder.id + ".jpg"}
          />
        </div>

        <div className="details">
          <h4>{finder.title}</h4>
          <p>{finder.brand}</p>
          <p>{finder.price}</p>

          <input
            type="text"
            onChange={(e) => {
              setNum(e.target.value);
            }}
          />
          <Bkbt onClick={() => window.open(`${finder.link}`)}>주문하기</Bkbt>
        </div>
      </div>
      <div className="detail">
        <Nav justify variant="tabs" defaultActiveKey="link-0">
          <Nav.Item>
            <Nav.Link
              onClick={() => {
                setPage(0);
              }}
              eventKey="link-0"
            >
              상세
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link
              onClick={() => {
                setPage(1);
              }}
              eventKey="link-1"
            >
              리뷰
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link
              onClick={() => {
                setPage(2);
              }}
              eventKey="link-2"
            >
              문의
            </Nav.Link>
          </Nav.Item>
        </Nav>
        <TabContent page={page} />
      </div>
    </div>
  );
}

function TabContent(props) {
  return (
    <div>
      {" "}
      {
        [
          <div>
            <Page1 />
          </div>,
          <div>
            <Page1 />
          </div>,
          <div>
            <Page1 />
          </div>,
        ][props.page]
      }{" "}
    </div>
  );
}

function Page1() {
  return (
    <div className="pages">
      <h2>체어원 코요테탄</h2>
      <h2>
        <div>Chair One Coyote Tan</div>
      </h2>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
      <p>
        헬리녹스의 베스트셀러 체어 원. 캠핑, 백 패킹 여행, 음악 페스티벌에서 뿐
        만 아니라 바다에서도 사용할 수 있는 체어 원의 다재다능함은 다른 의자와의
        비교를 단호히 거절합니다.
      </p>
    </div>
  );
}
